.about {
  &__title {
    margin: 0 0 32px;

    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $colorWhite;

    @include onTablet {
      margin-bottom: 0;
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__paragraph {
    font-family: "Inter", Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: $colorWhite;
    margin: 0;
  }
}
